} */
.frontside .card .card-body {
  padding: 0px;
}
.backside .card .card-body {
  padding: 5px;
}
.frontside .card .card-body img {
  /* width: 120px; */
  height: 120px;
}
.image-flip {
  height: calc(100% - 15px) !important;
}
